import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkState from 'linkstate';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_BOTTOM = mu.marginBottom();

import Error from '../../components/error';
import Message from '../../components/message';
import InviteFriend from '../../components/user/invite';
import User from '../../components/user';

@observer
class Profile extends Component {
	constructor(props) {
        super(props);
        this.state = {
            time: Date.now(),
			count: 10
        };
    }

	search = async () => {
        const { userStore } = this.props.stores;
        const { searchText } = this.state;
        await userStore.searchUsers({ search: searchText });
    }

	componentDidMount() {
		const { userStore } = this.props.stores;

		if (this.props.verifyEmailToken) {
			userStore.verifyEmail({ token: this.props.verifyEmailToken });
		}
	}

	render() {
		const { userStore } = this.props.stores;
		const { emailMessage, emailError, emailMessageIcon, emailErrorIcon, searchUsersResult } = userStore;
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='row'>
					<div class='col-12'>
						<Error message={emailError} icon={emailErrorIcon} remove={this.resetEmailError} />
                        <Message message={emailMessage} icon={emailMessageIcon} remove={this.resetEmailMessage}  />

						<h5><Text id='users.connect.title'>Finn dine venner</Text></h5>

                        <div class='form-row align-items-center'>
                            <div class='col-auto w-75'>
                                <label class='sr-only' for='inlineFormInput'><Text id='users.connect.search'>S칮k</Text></label>
                                <Localizer>
                                    <input type='text' class='form-control form-control-sm mb-2' id='inlineFormInput'
                                        placeholder={<Text id='users.connect.search-placeholder'>S칮k etter venner og bekjente</Text>}
                                        onInput={linkState(this, 'searchText')}
                                    />
                                </Localizer>
                            </div>
                            <div class='col-auto'>
                                <button type='button' class='btn btn-sm btn-primary mb-2' onClick={this.search}>
                                    <Text id='users.connect.doSearch'>S칮k</Text>
                                </button>
                            </div>
                        </div>
					</div>
					<div class='col-12 mt-3'>
                        {searchUsersResult && searchUsersResult.map(user => (
							<User stores={this.props.stores} user={user} />
						))}

                    </div>
                    <InviteFriend stores={this.props.stores} forceShow={1} />
				</div>
			</div>
		);
	}
}

export default Profile;
